// 1 Square
function Square(props) 
{
      return (
        <button 
          className="square" 
          onClick = {props.onClick}>
            {/* {console.log(props.onClick)} */}
          {props.value}
          {console.log(props.value)}
        </button>
      );
    }


  // 2 Board
  class Board extends React.Component {
    renderSquare(i) {
      // console.log(i);
      return(
      <Square
       value = {this.props.squares[i]}
       onClick ={() => {this.props.onClick(i)}}
       />
      );
    }

    
    render() {
      return (
        <div>
          <div className="board-row">
            
            {this.renderSquare(0)}
            {this.renderSquare(1)}
            {this.renderSquare(2)}
          </div>
          <div className="board-row">
          
            {this.renderSquare(3)}
            {this.renderSquare(4)}
            {this.renderSquare(5)}
          </div>
          <div className="board-row">
          
            {this.renderSquare(6)}
            {this.renderSquare(7)}
            {this.renderSquare(8)}
          </div>
        </div>
      );
    }
  }
  
  

  // 3 Game
  class Game extends React.Component {

    state = {
      hitory:[{
        squares:Array(9).fill(null),
      }],
      stepNumber: 0,
      xIsNext: true,
    }

  handelClick (i){
    const hitory = this.state.hitory.slice(0, this.state.stepNumber + 1)
    const current = hitory[hitory.length - 1];
    
    
    const squares = current.squares.slice();

    if (calculateWinner(squares) || squares[i]) {
       return;
      }
      squares [i] = this.state.xIsNext ? "X":"O";
      this.setState({
          hitory: hitory.concat([{
            squares: squares,
          }]),
          stepNumber: hitory.length,
          xIsNext: !this.state.xIsNext,
        });
  }

  jupmTo (step){
    this.setState({
      stepNumber: step,
      xIsNext: (step % 2) === 0,
    })
  }

    render() {
      const hitory = this.state.hitory;
      const current = hitory[this.state.stepNumber];
      const winner = calculateWinner(current.squares);

      const moves = hitory.map((step, move) =>{
        const desc = move ? "Go to move #" + move: "Go to game start"
        return(
        <li key = {move}>

          <button onClick = {() => this.jupmTo(move)}>
          {desc}
          </button>
          </li>)
      })

     let status;
    if (winner) {
      status = 'Winner: ' + winner;
    } else {
      status = 'Next player: ' + (this.state.xIsNext ? 'X' : 'O');
    }
      
      return (
        <div className="game">
          <div className="game-board">
            <Board 
            squares = {current.squares}
            onClick = {(i) => this.handelClick(i)}/>
          </div>
          <div className="game-info">
            <div>{status }</div>
            <ol>{moves}</ol>
           
          </div>
        </div>
      );
    }
  }
  
  // ========================================
  
  ReactDOM.render(
    <Game />,
    document.getElementById('root')
  );
  

//   calculateWinner                         ?
  function calculateWinner(squares) {
    const lines = [
      [0, 1, 2],
      [3, 4, 5],
      [6, 7, 8],

      [0, 3, 6],
      [1, 4, 7],
      [2, 5, 8],

      [0, 4, 8],
      [2, 4, 6],
    ];

    for (let i = 0; i < lines.length; i++) {
      const [a, b, c] = lines[i];  
      //console.log(squares);

      // ?
      if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
        console.log(squares[a]);
        // ?
       // console.log(squares)
        return squares[a];
      }
    }
    // ?
    return null;
   }
